.projectTile {
  display: flex;
  padding: 10px;
  background-image: var(--bg-linear-gradient);
  background-color: var(--main-background-color-alpha);
  color: var(--main-text-color);
  border-radius: 5px;
}

.projectDetails {
  flex-grow: 1;
  cursor: pointer;

  .wrapper {
    width: fit-content;
  }

  .projectName {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: bold;
    font-size: 20px;
    margin-bottom: 10px;
  }
  .projectDetailField {
    color: var(--main-text-color-alpha);
    font-size: 16px;
  }
}

.projectControls {
  display: flex;
  align-items: center;
}
